export default {
  props: {
    data: Object,
    index: Number
  },
  data() {
    return {
      toggle: true
    }
  },
  moudent() {
    console.log(this.data, this.index);
  },
  template:/* html */`
  <div>
    <div class="category-header" @click="toggle = !toggle">{{data.category}} <button type="button">{{toggle ? "Hide" : "Show"}}</button></div>
    <ul v-for="i in data.interactions" class="interactions" v-if="toggle">
      <li class="interaction" :class="i.method"><a :href="'./#interaction' + i.id">{{i.name}} <span>{{ i.method.toUpperCase() }}</span></a></li>
    </ul>
  </div>
  `,
}