export default {
  props: {
    response: Object,
    id: Number,
    category: String,
    index: Number
  },
  data() {
    return {
      res: [],
      toggle: false
    }
  },
  mounted() {
    this.res = this.response;
  },
  template: /* html */`
  <div class="response">
    <div class="res-ttl">
      <h5>Response<span class="code">{{res.code}}</span></h5>
      <input type="checkbox" v-model="toggle" :id="'toggle' + id + category + index"><label :for="'toggle' + id + category + index">{{toggle ? "Hide" : "Show"}}</label>
    </div>
    
    <figure v-if="toggle"><img :src="'./assets/img/response/' + res.body + '.png'"></figure>
  </div>
  `,
}