import Response from "./Response";
export default {
  components: {
    Response
  },
  props: {
    data: Array
  },
  template: /* html */`
  <section id="interactions">
    <h2 class="sec-ttl">インタラクション</h2>
    <div class="interaction-category" v-for="(d, index) in data" :key="index">
      <div class="interaction-header">
        <h3 class="interaction-category-title">{{ d.category }}</h3>
      </div>
      <div :id="'interaction' + i.id" class="interaction" v-for="(i, index) in d.interactions" :key="index">
        <div class="about">
          <h4 class="interaction-title">{{ i.name }}</h4>
          <ul>
            <li v-for="(a, index) in i.about" :key="index">{{ a }}</li>
          </ul>
        </div>
        <div class="info">
          <div class="info-header" :class="i.method">
            <div class="method">{{ i.method.toUpperCase() }}</div>
            <div class="url">{{ i.url }}</div>
          </div>
          <div class="info-body">
            <h5>Example URI</h5>
            <p class="example-uri">https://jpages.jp/JPagesApi/public/api<span class="hilight">{{ i.exampleUri }}</span></p>
            <div class="params" v-if="i.parameters && i.parameters.length">
              <h5>URI Parameters</h5>
              <ul>
                <li v-for="(p, index) in i.parameters" :key="index">
                  <strong class="hilight">{{ p.name }}</strong> : {{ p.type }} 
                  <span v-if="p.required">(required)</span>
                </li>
              </ul>
            </div>
            <div class="request">
              <h5>Request</h5>
              <div class="request-inner">
                <h6>Headers</h6>
                <ul>
                  <li v-for="(h, index) in i.request.headers" :key="index">{{ h }}</li>  
                </ul>
                <h6>Body</h6>
                <table v-if="i.request.parameters && i.request.parameters.length">
                  <tr><th>パラメーター名</th><th>型</th><th>Required</th><th>説明</th></tr>
                  <tr v-for="(p, index) in i.request.parameters" :key="index">
                    <th>{{ p.name }}</th>
                    <td>{{ p.type }}</td>
                    <td>{{ p.required }}</td>
                    <td>{{ p.desc }}</td>
                  </tr>
                </table>
                <p v-if="!i.request.parameters || !i.request.parameters.length">なし</p>
              </div>
            </div>
            <Response  v-for="(r, index) in i.response" :response="r" :id="i.id" category="d.category" :index="index"></Response>
          </div>
        </div>
      </div>
    </div>
  </section>
  `
}