export default {
  template: /* html */`
    <section id="authentication">
      <h2 class="sec-title">認証</h2>
      <ul>
        <li><p><strong>認証方式</strong>：Bearer token</p></li>
        <li>
          <h3>権限の種類</h3>
          <dl class="authentication-list">
            <div class="flex">
              <dt>管理者認証</dt>
              <dd>管理情報などを参照・追加・変更・消去が可能</dd>
            </div>
            <div class="flex">
              <dt>学生認証</dt>
              <dd>学生本人の情報や自分のチーム情報の変更が可能</dd>
            </div>
            <div class="flex">
              <dt>来場者認証</dt>
              <dd>来場者本人の情報の変更、自分の内定者を反映した情報参照が可能</dd>
            </div>
            <div class="flex">
              <dt>管理者・学生認証</dt>
              <dd>学生本人の情報やチームのパラメータの情報の変更が可能</dd>
            </div>
            <div class="flex">
              <dt>管理者・学生認証</dt>
              <dd>学生本人の情報やチームのパラメータの情報の変更が可能</dd>
            </div>
            <div class="flex">
            <dt>来場者・学生認証</dt>
            <dd>投票が可能</dd>
          </div>
          </dl>
        </li>
      </ul>
    </section>
  `, 
}